"use client"

import { useState } from "react"
import { Button } from "@/components/ui/button"
import {
  Card,
  CardContent,
  CardDescription,
  CardFooter,
  CardHeader,
  CardTitle,
} from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Badge } from "@/components/ui/badge"
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select"

interface Group {
  id: number
  name: string
  description: string
  category: string
  members: number
  posts: number
  imageUrl: string
  isJoined: boolean
  lastActive: string
  tags: string[]
}

const groups: Group[] = [
  {
    id: 1,
    name: "Tech Alumni Network",
    description: "A community for tech professionals to share insights and opportunities in the industry.",
    category: "Professional",
    members: 1250,
    posts: 456,
    imageUrl: "https://images.pexels.com/photos/3182812/pexels-photo-3182812.jpeg",
    isJoined: true,
    lastActive: "2024-02-15",
    tags: ["Technology", "Career", "Networking"],
  },
  {
    id: 2,
    name: "Entrepreneurship Hub",
    description: "Connect with fellow alumni entrepreneurs, share experiences, and find potential collaborators.",
    category: "Business",
    members: 850,
    posts: 325,
    imageUrl: "https://images.pexels.com/photos/3183150/pexels-photo-3183150.jpeg",
    isJoined: false,
    lastActive: "2024-02-14",
    tags: ["Startups", "Business", "Innovation"],
  },
  {
    id: 3,
    name: "Global Alumni Chapter",
    description: "Stay connected with alumni from around the world and participate in international networking events.",
    category: "Networking",
    members: 2100,
    posts: 789,
    imageUrl: "https://images.pexels.com/photos/3184418/pexels-photo-3184418.jpeg",
    isJoined: false,
    lastActive: "2024-02-13",
    tags: ["Global", "Networking", "Events"],
  },
]

export default function Groups() {
  const [searchQuery, setSearchQuery] = useState("")
  const [categoryFilter, setCategoryFilter] = useState("")

  const filteredGroups = groups.filter((group) => {
    const matchesSearch = 
      group.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
      group.description.toLowerCase().includes(searchQuery.toLowerCase())
    
    const matchesCategory = !categoryFilter || group.category === categoryFilter

    return matchesSearch && matchesCategory
  })

  return (
    <div className="container mx-auto py-8 px-4">
      <div className="max-w-6xl mx-auto">
        <div className="flex flex-col md:flex-row justify-between items-start md:items-center mb-8 gap-4">
          <div>
            <h1 className="text-3xl font-bold">Alumni Groups</h1>
            <p className="text-gray-600 mt-2">
              Join groups and engage with fellow alumni who share your interests
            </p>
          </div>
          <Button>Create New Group</Button>
        </div>

        {/* Search and Filters */}
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mb-8">
          <Input
            placeholder="Search groups..."
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
          />
          <Select value={categoryFilter} onValueChange={setCategoryFilter}>
            <SelectTrigger>
              <SelectValue placeholder="Filter by category" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="">All Categories</SelectItem>
              <SelectItem value="Professional">Professional</SelectItem>
              <SelectItem value="Business">Business</SelectItem>
              <SelectItem value="Networking">Networking</SelectItem>
              <SelectItem value="Social">Social</SelectItem>
            </SelectContent>
          </Select>
        </div>

        {/* Groups Grid */}
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          {filteredGroups.map((group) => (
            <Card key={group.id} className="flex flex-col">
              <div className="relative h-48">
                <img
                  src={group.imageUrl}
                  alt={group.name}
                  className="w-full h-full object-cover"
                />
                <Badge
                  className="absolute top-4 right-4"
                  variant={group.isJoined ? "default" : "secondary"}
                >
                  {group.isJoined ? "Joined" : group.category}
                </Badge>
              </div>
              <CardHeader>
                <CardTitle>{group.name}</CardTitle>
                <CardDescription>
                  Last active: {new Date(group.lastActive).toLocaleDateString()}
                </CardDescription>
              </CardHeader>
              <CardContent className="flex-1">
                <p className="text-gray-600 mb-4">{group.description}</p>
                <div className="flex flex-wrap gap-2 mb-4">
                  {group.tags.map((tag, index) => (
                    <Badge key={index} variant="outline">
                      {tag}
                    </Badge>
                  ))}
                </div>
                <div className="flex justify-between text-sm text-gray-500">
                  <span>{group.members} members</span>
                  <span>{group.posts} posts</span>
                </div>
              </CardContent>
              <CardFooter>
                <Button
                  variant={group.isJoined ? "outline" : "default"}
                  className="w-full"
                >
                  {group.isJoined ? "View Group" : "Join Group"}
                </Button>
              </CardFooter>
            </Card>
          ))}
        </div>

        {filteredGroups.length === 0 && (
          <div className="text-center py-8">
            <p className="text-gray-500">No groups found matching your criteria.</p>
          </div>
        )}

        {/* Create Group CTA */}
        <Card className="mt-12 bg-gray-50">
          <CardHeader className="text-center">
            <CardTitle>Don't see what you're looking for?</CardTitle>
            <CardDescription>
              Start your own group and build a community around your interests
            </CardDescription>
          </CardHeader>
          <CardContent className="text-center">
            <Button size="lg">Create a New Group</Button>
          </CardContent>
        </Card>
      </div>
    </div>
  )
}
